<template>
  <div style="width: 100%;height: 100vh;padding: 40px 44px;color:var(--color-neutral-10);max-width: 1550px;margin: 0 auto">
    <p style="font-size: 24px;font-weight: bold;margin-bottom: 20px"></p>
    <div style="width: 100%">
      <div style="margin-bottom: 20px">
        <p style="font-size: 16px;font-weight: bold;margin-bottom: 20px"></p>
        <div v-if="data.crawler.status!=undefined && data.crawler.status.status!=3" style="width: 100%;margin-bottom: 20px">
          <p style="font-size: 24px">{{ data.crawler.status.content }}</p>
          <div>
            <p v-if="data.crawler.status!=2">{{ data.crawler.status.results.taskName }}</p>
            <span>{{ data.crawler.status.status==2?(''):(' ' + (parseInt((data.crawler.status.status*10000))/100).toString() + '%') }}</span><br>
            <span>{{ data.crawler.status.status==2?('' + data.crawler.status.time):('' + data.crawler.status.results.elapsedTime + ' ' + data.crawler.status.results.remainingTime )}}</span>
            <v-progress-linear style="width: calc(100%);margin-bottom: 10px" :model-value="data.crawler.status.status * 100"></v-progress-linear>
            <v-btn v-if="data.crawler.status.status==2" variant="text" style="margin-right: 10px" @click="exportData"></v-btn>
            <v-btn v-if="data.crawler.status.status==2" variant="text" @click="exitTask"></v-btn>
          </div>
        </div>
        <div v-if="data.crawler.status!=undefined && data.crawler.status.status==3" style="width: 100%;margin-bottom: 20px">
          <p style="font-size: 24px">{{ data.crawler.status.content }}</p>
          <div>
            <p>{{ data.crawler.status.results.taskName }}</p>
            <v-btn variant="text" @click="exitTask"></v-btn>
          </div>
        </div>
        <div v-if="data.crawler.status==undefined">
          <p style="margin-bottom: 20px"></p>
          <div style="position: relative">
            <v-select
                v-model="data.crawler.selectedTaskName"
                :disabled="data.crawler.isStarting"
                label=""
                :items="['', '', '', '1', '2', '3', '', '', '', '']"
                variant="outlined"
                style="width: 400px;display: inline-block;min-width: 320px;margin-right: 5px"
            ></v-select>
            <v-select
                style="width: 100px;display: inline-block;margin-right: 5px"
                label=""
                v-if="data.crawler.selectedTaskName=='' || data.crawler.selectedTaskName=='' || data.crawler.selectedTaskName=='1' || data.crawler.selectedTaskName=='2' || data.crawler.selectedTaskName=='3'"
                variant="outlined"
                v-model="data.crawler.selectedProvinceName"
                @update:model-value="provinceChanged"
                :items="['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']"
            ></v-select>
            <v-select
                style="width: 100px;display: inline-block;margin-right: 5px"
                label=""
                v-if="(data.crawler.selectedTaskName=='' || data.crawler.selectedTaskName=='1' || data.crawler.selectedTaskName=='2' || data.crawler.selectedTaskName=='3') && data.crawler.selectedProvinceName!=''"
                variant="outlined"
                v-model="data.crawler.selectedTypeName"
                :items="data.crawler.selectedProvinceTypes"
            ></v-select>
            <v-btn @click="startTask" :disabled="data.crawler.selectedTaskName==''" :loading="data.crawler.isStarting" variant="text" style="display: inline-block;height: 56px;position: absolute;top: 0;"></v-btn>
          </div>

        </div>
      </div>
      <div>
        <!-- <p style="font-size: 16px;font-weight: bold;margin-bottom: 20px"></p> -->
      </div>
    </div>
  </div>
</template>

<script>
import {decodeString} from "@/utlis/tool";
const tool = require('@/utlis/tool')
const config = require('@/config')
const XLSX = require('xlsx')

export default {
  name: '',
  data() {
    return {
      data:{
        crawler:{
          status:undefined,
          isStarting:false,
          selectedTaskName:'',
          selectedProvinceName:'',
          provinceTypes:[],
          selectedTypeName:'',
          selectedProvinceTypes:[]
        },
        allowWSMsg:true
      },
      ws:undefined
    }
  },

  methods: {
    provinceChanged(){
      let re = []
      for(let i=0;i<this.data.crawler.provinceTypes.length;i++){
        if(this.data.crawler.provinceTypes[i].name==this.data.crawler.selectedProvinceName){
          for(let i1=0;i1<this.data.crawler.provinceTypes[i].type.length;i1++){
            re.push(this.data.crawler.provinceTypes[i].type[i1].name)
          }
        }
      }
      this.data.crawler.selectedProvinceTypes = re
    },
    async getProvinceTypes(){
      let re = await tool.axiosPost(config.serverURL,'/bg/crawler/SRTTypes',{
        token:this.bgData.loginedAdminInfo.token
      })
      this.data.crawler.provinceTypes = re.results
    },
    exportData(){
      window.open(config.serverURL + '/download/' + this.data.crawler.status.results,'_blank')
    },
    exitTask(){
      this.ws.send(tool.encodeString(JSON.stringify({
        token:this.bgData.loginedAdminInfo.token,
        operationName:'bgTaskExit'
      })))
    },
    async startTask(){
      this.data.crawler.isStarting = true
      let taskName = ''
      let data = {

      }
      //
      switch (this.data.crawler.selectedTaskName) {
        case '':
          data = {
            province:this.data.crawler.selectedProvinceName
          }
          break
        case '1':
          data = {
            yearSub:1,
            province:this.data.crawler.selectedProvinceName,
            type:this.data.crawler.selectedTypeName
          }
          break
        case '2':
          data = {
            yearSub:2,
            province:this.data.crawler.selectedProvinceName,
            type:this.data.crawler.selectedTypeName
          }
          break
        case '3':
          data = {
            yearSub:3,
            province:this.data.crawler.selectedProvinceName,
            type:this.data.crawler.selectedTypeName
          }
          break
        case '':
          data = {
            yearSub:0,
            province:this.data.crawler.selectedProvinceName,
            type:this.data.crawler.selectedTypeName
          }
          break
        case '':
          data = {}
          break
        case '':
          data = {}
          break
        case '':
          data = {}
          break
        case '':
          data = {}
          break
        case '':
          data = {}
          break
        case '':
          data = {}
          break
      }
      //
      switch (this.data.crawler.selectedTaskName) {
        case '':
          taskName = 'bgGetVolunteer'
          break
        case '1':
          taskName = 'bgGetSRT1'
          break
        case '2':
          taskName = 'bgGetSRT2'
          break
        case '3':
          taskName = 'bgGetSRT3'
          break
        case '':
          taskName = 'bgGetSRT0'
          break
        case '':
          taskName = 'bgGetSRTAll'
          break
        case '':
          taskName = 'bgGetSchoolInfo'
          break
        case '':
          taskName = 'bgGetSchoolSpecialInfo'
          break
        case '':
          taskName = 'bgGetSpecialInfo'
          break
        case '':
          taskName = 'bgGetAllSchoolImage'
          break
        case '':
          taskName = 'bgUpdateVolunteerBySchool'
          break
      }
      //
      let re = await tool.axiosPost(this.appData.serverURL,'/bg/task/startTask',{
        token:this.bgData.loginedAdminInfo.token,
        name:taskName,
        data:data
      })
      console.log({
        token:this.bgData.loginedAdminInfo.token,
        name:taskName,
        data:data
      })
      await tool.waitSeconds(3)
      this.data.crawler.isStarting = false
      if(re.status==-2){
        tool.axiosPostUnauthorized()
        return
      }
    }
  },

  // - DOM",html
  async mounted() {
    this.getProvinceTypes()

    const ws = new WebSocket(config.serverWSURL)
    this.ws = ws
    ws.onopen = (res) => {
      ws.send(tool.encodeString(JSON.stringify({
        token:this.bgData.loginedAdminInfo.token,
        operationName:'bgTaskStatus'
      })))
    }
    ws.onmessage = (res) => {
      let re = JSON.parse(decodeString(res.data))
      if(this.data.allowWSMsg==false){
        return
      }
      if(re.status==-2){
        this.data.allowWSMsg=false
        tool.axiosPostUnauthorized()
        return
      }
      switch (re.type) {
        case 'bgTaskStatus':
          if(re.status==-1){
            this.data.crawler.status = undefined
            return
          }
          this.data.crawler.status = re
          break
      }
    }
  },

  props: {
    appData:undefined,
    bgData:undefined
  },

  // data",
  computed: {},

  //data",
  watch: {},

  // - this",,html,html
  created() {

  },

  // - ",html
  beforeMount() {
  },

  // - ",,html
  beforeUpdate() {
  },

  //  - 
  beforeDestroy() {
  },

  // - ",,html
  updated() {
  },

  // - "
  destroyed() {
  },

  //keep-alive",
  activated() {
  }
}
</script>

<style scoped>

</style>